import React from 'react';
import Navigation from '../components/Navigation';
import Languages from '../components/knowleges/Languages';
import Experiences from '../components/knowleges/Experience';
import OtherSkills from '../components/knowleges/OtherSkills';
import Hobbies from '../components/knowleges/Hobbies';

const Knowledges = () => {
    return (
        <div className="knowleges">
            <Navigation />
            <div className="knowlegesContent">
                <Languages />
                <Experiences />
                <OtherSkills />
                <Hobbies />
            </div>   
        </div>
    );
};

export default Knowledges;